<template>
  <div class="home">
    <b-container>
      <b-form @submit.prevent.stop="submitHandle">
        <b-form-group label="Amount">
          <b-form-input
            type="number"
            v-model.number="$v.form.amount.$model"
            :state="validateState('amount')"
          ></b-form-input>
          <b-form-invalid-feedback>
            Number of quiz must be between 1 and 50.
          </b-form-invalid-feedback>
        </b-form-group>
        <b-form-group label="Category">
          <b-form-select v-model="form.selectedCategory" :options="categoryOptions"></b-form-select>
        </b-form-group>
        <b-form-group label="Difficult">
          <b-form-select v-model="form.selectedDifficult" :options="dificultOptions"></b-form-select>
        </b-form-group>
        <b-form-group label="Type">
          <b-form-select v-model="form.selectedType" :options="typeOptions"></b-form-select>
        </b-form-group>
        <b-form-group>
          <b-button variant="primary" block type="submit">OK</b-button>
        </b-form-group>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import { between } from "vuelidate/lib/validators";

export default {
  name: "Home",
  mounted() {
    this.fetchCategories();
  },
  validations: {
    form: {
      amount: {
        between: between(1, 50)
      }
    }
  },
  data() {
    return {
      form: {
        selectedType: null,
        selectedCategory: null,
        selectedDifficult: null,
        amount: 10
      },
      categoryOptions: [
        {
          value: null,
          text: "Any"
        }
      ],
      dificultOptions: [
        {
          value: null,
          text: "Any"
        },
        {
          value: "easy",
          text: "Easy"
        },
        {
          value: "medium",
          text: "Medium"
        },
        {
          value: "hard",
          text: "Hard"
        }
      ],
      typeOptions: [
        {
          value: null,
          text: "Any"
        },
        {
          value: "multiple",
          text: "Multiple Choice"
        },
        {
          value: "boolean",
          text: "True/False"
        }
      ]
    };
  },
  methods: {
    validateState(name) {
      const { $dirty, $error } = this.$v.form[name];
      return $dirty ? !$error : null;
    },
    fetchCategories() {
      axios.get(`https://opentdb.com/api_category.php`).then(response => {
        console.log(response);
        var categories = response.data["trivia_categories"];
        this.categoryOptions = categories.map(item => ({
          value: item.id,
          text: item.name
        }));
        this.categoryOptions.unshift({
          value: null,
          text: "Any"
        });
      });
    },
    submitHandle(event) {
      this.$v.form.$touch();
      if (this.$v.form.$anyError) {
        return;
      }
      var query = {
        amount: this.form.amount
      };
      if (this.form.selectedCategory !== null) {
        query.category = this.form.selectedCategory;
      }
      if (this.form.selectedDifficult !== null) {
        query.difficult = this.form.selectedDifficult;
      }
      if (this.form.selectedType !== null) {
        query.type = this.form.selectedType;
      }
      this.$router.push({ name: "Quiz", query });
    }
  }
};
</script>
