<template>
  <div>
    <b-container>
      <div v-if="isLoading">
        <b-spinner variant="success" label="Spinning"></b-spinner>
      </div>
      <b-form v-else @submit.prevent.stop="submitHandle">
        <b-form-group
          v-for="(quiz, index) in $v.displayQuizs.$each.$iter"
          :key="quiz.question"
          :label="`Question ${parseInt(index)+1}: ${quiz.$model.question}`"
        >
          <b-form-radio-group
            v-model="quiz.selectedAnswer.$model"
            :state="!isAnswered ? validateState(index) : quiz.$model.isCorrect"
            :options="quiz.$model.answerOptions"
            :disabled="isAnswered"
          >
            <b-form-invalid-feedback
              :state="!isAnswered ? validateState(index) : quiz.$model.isCorrect"
            >{{isAnswered ? `True answer is: ${quizs[parseInt(index)].correct_answer}`: "Please select one"}}</b-form-invalid-feedback>
            <b-form-valid-feedback :state="isAnswered && quiz.$model.isCorrect">True</b-form-valid-feedback>
          </b-form-radio-group>
        </b-form-group>
        <b-form-group>
          <b-button variant="success" type="submit">Submit</b-button>
        </b-form-group>
      </b-form>
    </b-container>
  </div>
</template>

<script>
import axios from "axios";
import { required } from "vuelidate/lib/validators";

export default {
  data() {
    return {
      quizs: [],
      displayQuizs: [],
      correctAnswerCount: 0,
      isAnswered: false,
      isLoading: true
    };
  },
  validations: {
    displayQuizs: {
      $each: {
        selectedAnswer: {
          required
        }
      }
    }
  },
  mounted() {
    this.fetchQuizs();
  },
  methods: {
    fetchQuizs() {
      axios
        .get(`https://opentdb.com/api.php`, { params: this.$route.query })
        .then(response => {
          this.isLoading = true;
          this.quizs = response.data.results;
          this.displayQuizs = this.quizs.map(item => {
            var answerOptions = [];
            if (item.type === "boolean") {
              answerOptions = ["True", "False"];
            } else {
              answerOptions = this.shuffle([
                item.correct_answer,
                ...item.incorrect_answers
              ]);
            }
            return {
              question: item.question,
              selectedAnswer: null,
              answerOptions
            };
          });
          this.isLoading = false;
        });
    },
    shuffle(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]];
      }
      return array;
    },
    validateState(index) {
      const { $dirty, $error } = this.$v.displayQuizs.$each.$iter[index];
      return $dirty ? !$error : null;
    },
    submitHandle() {
      this.$v.displayQuizs.$touch();
      if (this.$v.displayQuizs.$anyError) {
        return;
      }
      this.isAnswered = true;
      this.correctAnswerCount = 0;
      this.displayQuizs.forEach((item, index) => {
        if (this.quizs[index].correct_answer === item.selectedAnswer) {
          this.correctAnswerCount++;
          item.isCorrect = true;
        } else {
          item.isCorrect = false;
        }
      });
      alert(this.correctAnswerCount);
    }
  }
};
</script>

